___CSS_LOADER_EXPORT___.push([module.id, `* {
    box-sizing: border-box;
}

ul {
    text-align: center;

}

li {
    list-style: none;
    display: inline-block;
    margin-left: 2.5rem;
}

body {
    font-family: 'Bricolage Grotesque', sans-serif;
}

.container {
    width: 80%;
    max-width: 1200px;
    margin: 0 auto;
}

.active {
    /* background-color: rgb(209, 209, 209); */
}

nav {
    padding: .5rem;
}

nav a {
    display: inline-block;
    padding: 10px 20px;
    border: 1px solid black;
    border-radius: 6px;
    cursor: pointer;
}

/* home */
#home {
    text-align: center;
}

#home h1 {
    font-size: 3rem;
}

#home h2 {
    text-align: left;
}

#home p {
    font-size: 1.1rem;
    font-weight: 500;
    line-height: 1.3;
}

.restaurant-name,
.intro,
.opening-hours,
.location {
    width: 40rem;
    margin: 0 auto;
    border: 4px solid black;
    padding: 1rem 2rem;
    margin-bottom: 3rem;

}

.restaurant-name {
    margin-top: 3.5rem;

}

.intro {
    text-align: justify;
}

/* menu */
#menu>div,
#menu>h1,
#menu>h2 {
    width: 40rem;
    margin: 0 auto;
    border: 4px solid black;
    padding: 1rem 2rem;
    margin-bottom: 3rem;

}

#menu>h1,
#menu>h2 {
    text-align: center;
}

#menu>h1 {
    font-size: 3rem;
}

#menu>h2 {
    font-size: 2rem;
}

#menu img {
    width: 80%
}

#menu p {
    font-size: 1.1rem;
    font-weight: 500;
    line-height: 1.5;
}

/* contact */
.chef,
.waiter,
.manager {
    width: 40rem;
    margin: 0 auto;
    border: 4px solid black;
    padding: 1rem 2rem;
    margin-bottom: 3rem;
}

/* footer */
footer {
    text-align: center;
    padding: 1rem;
